import { Fade } from "react-awesome-reveal";
import './Nav.css';
import React from 'react'
import { useState } from 'react';
import { useEffect } from 'react';

import { Link } from 'react-scroll';

const Nav = () => {
const [click, setClick] = React.useState(false);

const handleClick = () => setClick(!click);
const Close = () => setClick(false);

const [navstate,Setnavstate]=useState("navbar")


  useEffect(()=>{
     document.addEventListener("scroll", e => {
      
      if (window.scrollY >= 50) {
        if (navstate !== "nav-sticky shadow-2xl shadow-black shadow-2xl") {
          Setnavstate("nav-sticky shadow-2xl shadow-black shadow-2xl")
        }
      } else {
        if (navstate !== "navbar") {
          Setnavstate("navbar")
        }
      }
    })
   
    
  },[navstate])


return (
  <div>
   <div className={click ? "main-container" : ""} onClick={()=>Close()} />
    <nav className={navstate+"  "} onClick={(e)=>e.stopPropagation()}>
      <div className="nav-container">
        
        
        <Fade cascade direction="left" >
        <ul className={click ? "nav-menu active" : "nav-menu"}>
        
          <li className="nav-item">
            <Link
              exact
              to="link1"
              smooth={true}
              activeClassName="active"
              className="nav-links"
              onClick={click ? handleClick : null}
            >
              01.About
            </Link>
          </li>
         
          <li className="nav-item">
            <Link
              exact
              to="link2"
              smooth={true}
              activeClassName="active"
              className="nav-links"
              onClick={click ? handleClick : null}
            >
              02.Work
            </Link>
          </li>
          <li className="nav-item">
            <Link
              exact
              to="link3"
              activeClassName="active"
              smooth={true}
              className="nav-links"
             onClick={click ? handleClick : null}
             
            >
              03.Contact
            </Link>
          </li>
          <li className=""> 
          
          <a target={"_blank"} href="https://avi-cr17.github.io/html-resume/" >
            
            <button type="button" className="text-teal-400  border border-teal-400 hover:bg-teal-600 focus:ring-4 focus:outline-none focus:ring-teal-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center ml-5 mb-2 
            border-teal-300 dark:text-teal hover:text-teal-400 dark:hover:bg-teal-300/[0.2] ">Resume</button>
            </a>
          </li>
        </ul>
        </Fade>
        

        <div className="nav-icon" onClick={handleClick}>
          <i className={click ? "fa fa-times" : "fa fa-bars"}></i>
        </div>
      </div>
    </nav>
  </ div>
        
    )
}

export default Nav